.square {
    background: black;
    border: 2px solid turquoise;
    float: left;
    font-size: 30px;
    font-weight: bold;
    line-height: 50px;
    height: 80px;
    margin-right: 1px;
    margin-top: 1px;
    padding: 0;
    text-align: center;
    width: 80px;
    color: white;
}
  
.square:focus {
  outline: none;
}

.square:hover{
  box-shadow: .5px .5px 10px 3px #0ff;
}